@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");

.navBarContainer {
  display: flex;
  justify-content: space-between;
  padding-left: 60px;
  padding-right: 40px;
  transition: all 0.6s ease;
}

.navBarContainer a {
  color: white;
}

.logoContainer a:hover {
  color: white;
  cursor: pointer;
}

.navBarContainer:hover {
  background-color: red;
}

.navBarContainer a:hover {
  color: black;
  cursor: pointer;
}

.navBarContainer a:visited {
  color: white;
  cursor: pointer;
}

.navBarContainer a:visited:hover {
  color: black;
  cursor: pointer;
}

.logoContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 36px;
}
.logoContainer a {
  text-decoration: none;
}

.genreListContainer {
  font-family: "Bebas Neue", cursive;
  font-size: 28px;
}

.genreListContainer > ul {
  display: flex;
  list-style: none;
  gap: 20px;
}
.snsListContainer {
  display: flex;
  align-items: end;
}

.snsListContainer > ul {
  padding: 0;
  width: 70px;
  display: flex;
  list-style: none;
  justify-content: space-between;
}
.snsListContainer a {
  text-decoration: none;
}
