.movieContainer {
  box-shadow: 3px 3px 0.3em red;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: all 0.4s ease;
  width: 400px;
}

.movieContainer:hover {
  transform: translate(0, -3%);
  box-shadow: 5px 5px 1em red;
}
.movieContainer:hover .coverImage {
  box-shadow: 3px 3px 1em purple;
  transform: translate(0, 3%);
}
.movieTitle {
  box-sizing: border-box;
}

.coverImage {
  box-shadow: 1px 1px 0.3em purple;
  transition: all 0.4s ease;
}
.title {
  font-family: cursive;
}

.sliderContainer {
  width: 95%;
  padding-left: 40px;
}

.sliderContainer .nextArrow {
  position: sticky;
}
.sliderContainer .prevArrow {
  position: sticky;
}
